import {BaseConnectorConfig, isBaseConnectorConfigType} from "@xip-online-data/types";

export interface YamlConfig extends BaseConnectorConfig {
  interval?: number;
}

export function isYamlConfigType(obj: YamlConfig): obj is YamlConfig {
  return (
    obj !== undefined &&
    obj !== null &&
    isBaseConnectorConfigType(obj)
  );
}
